@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Nanum+Gothic&family=PT+Serif&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Nanum+Gothic&family=Noto+Sans+KR:wght@300&family=PT+Serif&display=swap');

@media only screen and (min-width: 1300px) {
    body {
        margin: 0;
        font-family: 'PT Serif', serif;
        overflow-x: hidden;
    }

    /*헤더 css*/
    .header {
        position: relative;

    }

    .header_img {
        position: relative;
        overflow: hidden;
        width: 100vw;
        height: 100vh;
    }

    .header_img img {
        position: absolute;
        width: 100vw;
        top: -1480px;
    }

    .header_text {
        display: flex;
        align-items: center;
        justify-content: space-around;
        position: absolute;
        top: 10px;
        width: 100%;
        color: white;
    }

    .header_text a{
        text-decoration: none;
        color:white;
    }

    .header_text h2 {
        font-weight: lighter;
        font-size: 24px;
        margin: 0;
        padding: 0px 20px 0px 20px;
    }

    .header_nav {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;
    }

    .header_nav li {
        font-size: 22px;
        color: white;
        padding: 0px 20px 0px 20px;
        margin: 0;
    }

    .header_nav a {
        text-decoration: none;
    }

    .header_login {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 0;
    }

    .header_login li {
        font-size: 22px;
        padding: 0px 20px 0px 20px;
        margin: 0;
        cursor: pointer;
    }

    .header_login a {
        text-decoration: none;
    }

    .header_hidden_en{
        position: absolute;
        right:385px;
        background-color: #bab56c;
        border-radius: 3px;
        max-height: 0px;
        overflow: hidden;
    }

    .header_text button{
        display:none;
    }

    .header_nav_hidden_shop{
        position: absolute;
        top:50px;
        width:100%;
        color: #fffef2;
        left: 0px;
        max-height: 0;
        overflow: hidden;
        background-color: #bab56c;
    }

    .header_nav_hidden_about{
        position: absolute;
        top:50px;
        width:100%;
        color: #fffef2;
        left:0px;
        max-height: 0px;
        overflow: hidden;
        background-color: #bab56c;
    }

    .header_nav_hidden_shop ul {
        display:flex;
        flex-direction: column;
        align-items: center;
        list-style: none;
        padding:0px 10px 0px 10px;
        margin:0;
        margin-right:480px;
    }

    .header_nav_hidden_about ul {
        display:flex;
        flex-direction: column;
        align-items: center;
        list-style: none;
        padding:0px 10px 0px 10px;
        margin:0px;
        margin-right:245px;
    }

    .header_nav_hidden li{
        font-size:22px;
        padding:10px 0px 10px 0px;
        cursor:pointer;
    }

    .header_nav_hidden li:hover{
        color:#c2c2af;
    }

    /*베스트셀러 배너 css*/
    .bestseller_banner {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: #c2c2af;
        padding: 40px 0px 40px 0px;
    }

    .bestseller_banner h2 {
        font-weight: lighter;
        font-size: 35px;
        text-align: center;
        margin: 0px 0px 30px 0px;
    }

    .bestseller_banner div button {
        color: #252525;
        font-size: 17px;
        border: none;
        background-color: transparent;
        padding: 0px 10px 0px 10px;
        margin: 0px 5px 0px 5px;
        cursor: pointer;
    }

    /*베스트셀러 컴포넌트 div css*/
    .bestseller_perfume{
        margin-top:50px;
    }
    /*베스트셀러 공용 css*/
    .bestseller_grid {
        display: grid;
        grid-template-areas:
            "A B C D";
        column-gap: 10px;
        margin:10px 10px 100px 10px;
    }

    .bestseller_one {
        position: relative;
        grid: A;
    }

    .bestseller_one_img {
        width: 464px;
        height: 464px;
        cursor:pointer;
    }

    .bestseller_one_img img {
        position: absolute;
        display: flex;
        justify-content: center;
        width: 464px;
        transition:  all 1s;
    }

    .bestseller_two {
        position: relative;
        grid: B;
    }

    .bestseller_two_img {
        width: 464px;
        height: 464px;
        cursor:pointer;
    }

    .bestseller_two_img img {
        position: absolute;
        display: flex;
        justify-content: center;
        width: 464px;
        transition:  all 1s;
    }

    .bestseller_three {
        position: relative;
        grid: C;
    }

    .bestseller_three_img {
        width: 464px;
        height: 464px;
        cursor:pointer;
    }

    .bestseller_three_img img {
        position: absolute;
        display: flex;
        justify-content: center;
        width: 464px;
        transition:  all 1s;
    }

    .bestseller_four {
        position: relative;
        grid: D;
    }

    .bestseller_four_img {
        width: 464px;
        height: 464px;
        cursor:pointer;
    }

    .bestseller_four_img img {
        position: absolute;
        display: flex;
        justify-content: center;
        width: 464px;
        transition:  all 1s;
    }

    .bestseller_grid_text{
        display:flex;
        justify-content: space-between;
        padding:0px 5px 0px 5px;
        border-bottom: 1px solid #252525;
    }
    .bestseller_grid_text h3{
        font-weight: lighter;
        font-size:18px;
    }

    .bestseller_grid_text span{
        display:flex;
        align-items: center;
    }

    .bestseller_grid_text span div{
        font-size:13px;
        padding:2px 15px 0px 0px;
    }

    .bestseller_grid_text span h3{
        font-size:17px;
    }

    .bestseller_perfume_text{
        padding-left:5px;
    }

    /*스토리배너 css*/
    .story_banner{
        position: relative;
        background-color: #fffef2;
        border-bottom: 1px solid #252525;
    }
    .story_banner h2{
        font-size: 35px;
        text-align: center;
        font-weight: lighter;
        padding:50px 0px 0px 0px;
        margin:0;
    }

    .stoty_banner_container{
        display:flex;
        width:900px;
        margin:auto;
        margin-top:100px;
        margin-bottom: 100px;
    }

    .stoty_banner_container img{
        width:450px;
    }

    .story_banner_text_container{
        display:flex;
        flex-direction: column;
        align-items: center;
        margin:0px 60px 0px 60px;
    }

    .story_banner_text_container div{
        text-align: center;
    }

    .story_banner_text_container div h4{
        margin:40px 0px 5px 0px;
        font-size:30px;
        font-weight: lighter;
    }
    .story_banner_text_container div p{
        margin:0px;
        font-size:13.5px;
        font-family: 'Noto Sans KR', sans-serif;
        color:#252525;
        font-weight:bold;
    }
    .story_banner_text_container div h6{
        font-weight: lighter;
        font-size:16px;
        margin:10px 0px 5px 0px;
    }

    .story_banner_text_container p{
        font-size:13.5px;
        margin:20px 0px 0px 0px;
        color:#252525;
        font-family: 'Noto Sans KR', sans-serif;
    }
    .story_banner_text_container p:last-child{
        font-size:13.5px;
        margin:20px 0px 0px 0px;
        color:#252525;
        font-family: 'PT Serif', serif;    
    }

    .story_banner_sidetext{
        position: absolute;
        top:1200px;
        right: -1080px;
        transform: rotate(90deg);
        font-size:400px;
        opacity: 0.2;
    }

    .footer{
        display:flex;
        justify-content: space-between;
        padding:0px 50px 100px 50px;
    }

    .footer h3{
        font-size:20px;
        font-weight: lighter;
        font-family: 'PT Serif', serif; 
    }

    .footer div{
        font-size:13.5px;
        font-family: 'Noto Sans KR', sans-serif;
        padding:3px 0px 3px 0px;
    }
    .footer_customer ul{
        display:flex;
        padding:0;
        margin:0;
        list-style: none;
        font-family: 'Noto Sans KR', sans-serif;
    }

    .footer_customer ul li{
        padding:0px 3px 0px 3px;
    }

    .footer_customer ul li:first-child{
        padding-left:0;
        border-right: 1px solid #abacb1;
    }
    .footer_customer ul li:last-child{
        border-left: 1px solid #abacb1;
    }

    .footer_service{
        display:flex;
        justify-content: space-evenly;
        width:800px
    }

    .footer_service a{
        text-decoration: none;
        color:black;
    }

}

